<template>
  <div class="account-info">
    <div v-title>{{ title }}</div>
    <ul>
      <li>
        <span class="list-name">合作商名称</span>
        <span>星巴克店·专注中大校园的公益行</span>
      </li>
      <li>
        <span class="list-name">合作商图标</span>
        <img src="../../assects/images/key-no.png"/>
      </li>
      <li>
        <span class="list-name">合作商ID</span>
        <span>8000887761</span>
      </li>
      <li>
        <span class="list-name">联系人姓名</span>
        <span>卢瑶瑶</span>
      </li>
      <li>
        <span class="list-name">联系人电话</span>
        <span>13133456688</span>
      </li>
      <li>
        <span class="list-name">联系人邮箱</span>
        <span>13133456688@qq.com</span>
      </li>
      <li>
        <span class="list-name">登录密码</span>
        <span>已设置</span>
      </li>
    </ul>
    <div v-show="hasLogin" class="logout-box">
      <div @click="Logout" class="logout">退出登录</div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        title: '账号资料',
        hasLogin: true
      }
    },
    created () {
      this.$laMsg('合作商名称为注册时填写，若需修改请联系噢啦运营')
    },
    methods: {
      Logout () {
      }
    }
  }
</script>

<style lang="less" scoped>
  .account-info {
    background-color: white;
    ul {
      li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 17px 15px;
        font-size: 14px;
        color: #9D9DA5;
        line-height: 14px;
        border-bottom: 1px solid #E9ECF5;
        .list-name {
          font-size: 16px;
          color: #191D21;
          line-height: 16px;
        }
        img {
          width: 50px;
          height: 50px;
        }
      }
    }
  }
  .logout-box {
    display: flex;
    justify-content: center;
    margin-top: 110px;
    .logout {
      width: 240px;
      padding: 13.5px 0;
      font-size: 16px;
      color: #FFB717;
      line-height: 18px;
      border: 1px solid #FFB717;
      border-radius: 8px;
      display: flex;
      justify-content: center;
      align-items: center;
      display: -webkit-flex;
      -webkit-justify-content: center;
      -webkit-align-items: center;
    }
  }
</style>



